.columns{


    .basetheme-black-white{
      @import './components/ColourMode/Basetheme-black-white';
      background-color: #FFFFFF !important;
    }

    .basetheme-dark{
      @import './components/ColourMode/Basetheme-dark';
      background-color: #212529 !important;
    }

    .basetheme-light{
      @import './components/ColourMode/Basetheme-light';
      background-color: #E9ECEF !important;
    }

    /**NOTE: Responsive FONT-SIZE is defined on 'Basetheme.scss' file **/
    h1, h2, h3, h4, h5, h6{
      font-weight: 200 !important;
      letter-spacing: 0.02rem;
    }

    p, a{
      font-weight: 100 !important;
      letter-spacing: 0.02rem;
    }

    .btn{
      font-weight: 300 !important;
      letter-spacing: 0.1rem;
      text-transform: uppercase;
    }

}